.mehedi form {
    max-width: 500px;
    margin: 0 auto;
  }
  
 .mehedi h1 {
    font-weight: 100;
    color: rgb(141, 110, 110);
    text-align: center;
    padding-bottom: 10px;
    border-bottom: 1px solid rgb(79, 98, 148);
  }
  
  .mehedi .form {
    background: #0e101c;
    max-width: 400px;
    margin: 0 auto;
  }
  
  .mehedi p {
    color: #bf1650;
  }
  
  .mehedi p::before {
    display: inline;
    content: "⚠ ";
  }
  
  .mehedi input {
    display: block;
    box-sizing: border-box;
    width: 100%;
    border-radius: 4px;
    border: 1px solid rgb(19, 15, 15);
    padding: 10px 15px;
    margin-bottom: 10px;
    font-size: 14px;
  }
  
  .mehedi label {
    line-height: 2;
    text-align: left;
    display: block;
    margin-bottom: 13px;
    margin-top: 20px;
    color: rgb(68, 15, 15);
    font-size: 14px;
    font-weight: 200;
  }
  
  .mehedi input[type="submit"] {
    background: #2d489e;
    color: rgb(255, 251, 251);
    text-transform: uppercase;
    border: none;
    margin-top: 40px;
    padding: 20px;
    font-size: 16px;
    font-weight: 100;
    letter-spacing: 10px;
  }
  
  .mehedi input[type="submit"]:hover {
    background: #58303e;
  }
  
  .mehedi input[type="button"]:active,
  .mehedi input[type="submit"]:active {
    transition: 0.3s all;
    transform: translateY(3px);
    border: 1px solid transparent;
    opacity: 0.8;
  }
  
  .mehedi input:disabled {
    opacity: 0.4;
  }
  
  .mehedi  input[type="button"]:hover {
    transition: 0.3s all;
  }
  
  .mehedi input[type="button"],
  .mehedi input[type="submit"] {
    -webkit-appearance: none;
  }
  
  .mehedi {
    max-width: 600px;
    margin: 0 auto;
  }
  