.formcv{
    background-color: #0e101c;
}


.formcv form {
    max-width: 500px;
    margin: 0 auto;
    color:#7808e034
    
  }
  
  .formcv h1 {
    font-weight: 100;
    color: white;
    text-align: center;
    padding-bottom: 10px;
    border-bottom: 1px solid rgb(79, 98, 148);
  }
  
  .formcv  .form {
    background: #0e101c;
    max-width: 400px;
    margin: 0 auto;
  }
  
  .formcv p {
    color: #bf1650;
  }
  
  .formcv  p::before {
    display: inline;
    content: "⚠ ";
  }
  
  .formcv input {
    display: block;
    box-sizing: border-box;
    width: 100%;
    border-radius: 4px;
    border: 1px solid white;
    padding: 10px 15px;
    margin-bottom: 10px;
    font-size: 14px;
  }
  
  .formcv label {
    line-height: 2;
    text-align: left;
    display: block;
    margin-bottom: 13px;
    margin-top: 20px;
    color: white;
    font-size: 14px;
    font-weight: 200;
  }
  
  .formcvbutton[type="submit"],
  .formcv input[type="submit"] {
    background: #ec5990;
    color: white;
    text-transform: uppercase;
    border: none;
    margin-top: 40px;
    padding: 20px;
    font-size: 16px;
    font-weight: 100;
    letter-spacing: 10px;
  }
  
  .formcv button[type="submit"]:hover,
  .formcv input[type="submit"]:hover {
    background: #bf1650;
  }
  
  .formcv button[type="submit"]:active,
  .formcv input[type="button"]:active,
  .formcv input[type="submit"]:active {
    transition: 0.3s all;
    transform: translateY(3px);
    border: 1px solid transparent;
    opacity: 0.8;
  }
  
  .formcv input:disabled {
    opacity: 0.4;
  }
  
  .formcv input[type="button"]:hover {
    transition: 0.3s all;
  }
  
  .formcv button[type="submit"],
  .formcv input[type="button"],
  .formcv input[type="submit"] {
    -webkit-appearance: none;
  }
  
  
  .formcv button[type="button"] {
    display: block;
    appearance: none;
    background: #333;
    color: white;
    border: none;
    text-transform: uppercase;
    padding: 10px 20px;
    border-radius: 4px;
  }
  
  .formcv hr {
    margin-top: 30px;
  }
  
  .formcv  button {
    display: block;
    appearance: none;
    margin-top: 40px;
    border: 1px solid #333;
    margin-bottom: 20px;
    text-transform: uppercase;
    padding: 10px 20px;
    border-radius: 4px;
  }
  


  @media (max-width: 566px) {

    .formcv  .form {
      background: #0e101c;
      max-width: auto;
      margin: 0;
    }

   
  }


  @media (max-width: 450px) {

    .formcv  button {
      display: block;
      appearance: none;
      margin-top: 40px;
      border: 1px solid #333;
      margin-bottom: 20px;
      text-transform: uppercase;
      padding: 0px 0px;
      border-radius: 4px;
    }
    

   
  }


  