@media screen and (max-width: 576px) {
  .nayeemContainer {
    background: #0c0c0c;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 0px;
    height: 800px;
    position: relative;
    z-index: 1;
  }

  .nayeemColor {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }

  .vdo {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    background: #232a34;
  }
  .nayeemContent {
    z-index: 3;

    position: absolute;

    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
